import apiBase from "@/module/api/apiBase.js";
import UserAgentUtil from "@/module/UserAgentUtil.js";

const identifyItems = (() => {
  function measureTextWidth(text, font) {
    const canvas = document.createElement("canvas");
    const context = canvas.getContext("2d");
    context.font = font;
    const metrics = context.measureText(text);
    return metrics.width;
  }

  return {
    getFonts() {
      const fonts = [];
      const testText =
        "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
      for (let j = 0; j < testText.length; j++) {
        let font = testText[j] + " font";
        let width = measureTextWidth(testText[j], font);
        fonts.push({
          font: font,
          width: width,
        });
      }
      return fonts;
    },
    getLanguage() {
      return navigator.language || navigator.userLanguage;
    },
    getScreenSize() {
      return {
        width: window.screen.width,
        height: window.screen.height,
      };
    },
  };
})();

const api = apiBase();

const getIpAddress = () => {
  return api.get("https://api.ipify.org?format=json").then((response) => {
    return response.data.ip;
  });
};

export default (() => {
  return {
    get() {
      return Promise.all([getIpAddress()])
        .then((results) => {
          const ip = results[0];

          let fp = {};
          if (ip) {
            fp.ip = ip;
          }

          const userAgentInfo = UserAgentUtil.get();
          userAgentInfo.language = identifyItems.getLanguage();
          userAgentInfo.screenSize = identifyItems.getScreenSize();
          fp = { ...userAgentInfo, ...fp };

          return fp;
        })
        .catch((error) => {
          console.error(error);
        });
    },
    toSHA256(browserInfo) {
      const textAsBuffer = new TextEncoder().encode(
        JSON.stringify(browserInfo)
      );
      return window.crypto.subtle
        .digest("SHA-256", textAsBuffer)
        .then((hashBuffer) => {
          const hashArray = Array.from(new Uint8Array(hashBuffer));
          const hash = hashArray
            .map((item) => item.toString(16).padStart(2, "0"))
            .join("");

          return hash;
        });
    },
    platform() {
      const userAgent = navigator.userAgent.toLowerCase();
      if (/iphone|ipad|ipod/.test(userAgent)) {
        return "ios";
      } else if (/android/.test(userAgent)) {
        return "android";
      } else {
        return "web";
      }
    },
    testFilter(identify) {
      const result = {};
      if (identify.os && identify.os.name) {
        result.os = { name: identify.os.name };
      }
      return result;
    },
  };
})();
