<template>
  <div>
    <span v-if="message">{{ message }}</span>
  </div>
</template>

<script>
import FingerprintUtil from "@/module/FingerprintUtil";

export default {
  name: "Agent",
  data() {
    return {
      platform: "",
      linkContent: "",
      message: "",
    };
  },
  created() {
    this.platform = FingerprintUtil.platform();
  },
  mounted() {
    let identify = {};
    let hash = "";
    const fp = FingerprintUtil.get()
      .then((result) => {
        identify = FingerprintUtil.testFilter(result);
        return FingerprintUtil.toSHA256(identify);
      })
      .then((result) => {
        hash = result;
        this.message = JSON.stringify({
          agent: identify,
          hash: hash,
        });
        this.toAppScript(this.message);
      });
  },
  methods: {
    toAppScript(content) {
      const result = content || "EMPTY";

      if (this.platform === "ios") {
        if (window.webkit) {
          window.webkit.messageHandler.getAppLink.postMessage(result);
        }
      }
      if (this.platform === "android") {
        if (window.Android) {
          window.Android.getAppLink(result);
        }
      }
    },
  },
};
</script>
